module.exports = "/* ==== Color ==== */\n/* ==== responsive screen ==== */\n.team .bg-lightYellow {\n  box-shadow: 0 20px 30px -16px rgba(9, 9, 16, 0.2);\n}\n.team--block {\n  margin-top: 100px;\n  margin-bottom: 80px;\n}\n@media screen and (max-width: 767.98px) {\n  .team--block {\n    margin-top: 80px;\n    margin-bottom: 50px;\n  }\n}\n.team--block:hover {\n  transform: scale(1.05);\n}\n.team--thumb {\n  width: 100%;\n  position: absolute;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n.team--thumb img {\n  width: 250px;\n  height: 250px;\n  -o-object-fit: cover;\n     object-fit: cover;\n  border-radius: 50%;\n  border: 3px solid #d69709;\n  transition: margin-top 1000ms, box-shadow 300ms;\n}\n.team--thumb img:hover {\n  box-shadow: 0 0 10px #ffb612;\n  margin-top: 10px;\n}\n@media screen and (max-width: 767.98px) {\n  .team--thumb img {\n    width: 180px;\n    height: 180px;\n  }\n}\n.team--detail {\n  padding: 160px 30px 0px 30px;\n  font-size: 1.4rem;\n  margin-top: 50px;\n}\n@media screen and (max-width: 767.98px) {\n  .team--detail {\n    padding: 110px 15px 0px 15px;\n  }\n}\n.team--detail p {\n  margin-bottom: 0;\n}\n.team--detail h3 span {\n  font-size: 1.6rem;\n  font-weight: bold;\n  line-height: 2;\n  padding-bottom: 10px;\n}\n@media screen and (max-width: 480px) {\n  .team--detail h3 span {\n    font-size: 1.4rem;\n  }\n}\n.team--detail .position {\n  font-size: 1.2rem;\n  font-weight: 600;\n  text-align: center;\n  margin-bottom: 30px;\n}\n@media screen and (max-width: 480px) {\n  .team--detail .position {\n    font-size: 1.4rem;\n  }\n}\n.team--detail .position::before {\n  content: \"\";\n  width: 3em;\n  height: 3px;\n  display: block;\n  margin: 0.7em auto 0.5em;\n  background-color: #d69709;\n}\n/*# sourceMappingURL=data:application/json;base64, */"